

































































import { Component, Vue, Watch } from 'vue-property-decorator';
import io from 'socket.io-client';

@Component({
  components: {
    
  },
})
export default class RemoteController extends Vue {
  private socket;

  private alpha: number = 0;
  private beta1: number = 0;
  private beta2: number = 0;
  private beta3: number = 0;
  private gamma: number = 0;
  private n: number = 0;

  private mounted() {
    this.socket = io({
      transports: ['websocket']
    });

    this.socket.on('connect', () => {
      this.n = 100;
      this.gamma = 1;

      this.sendControlMessage();
    });

    this.socket.on('disconnect', () => {
    });
  }

  get controlMessage() {
    return {
      n: this.n,
      alpha: this.alpha,
      beta1: this.beta1,
      beta2: this.beta2,
      beta3: this.beta3,
      gamma: this.gamma,
    };
  }

  @Watch('controlMessage')
  private sendControlMessage() {
    if (!this.socket)
      return;

    this.socket.emit('control', this.controlMessage);
  }
}
